/**
 *  使用通配符获取模块数组
 *  r: require.context()
 *  type: 'router' | 'store'
 */
exports.importAll = function (r, type) {

    if (type !== 'router' && type !== 'store') {
        return console.warn("expected 'router' or 'store' only");
    }

    var list = [];

    r.keys().forEach(function (url) {
        var page = r(url).default;

        if (type === 'router') {
            list.push({
                path: page.path,
                name: page.name,
                component: page
            });
        }

        if (type === 'store') {
            list[page.namespace] = page;
        }
    });

    return list;
};

